width: 100%;
object-fit: cover;
}
.frameTitle{
    font-weight: 600;
    color: #000;
    font-family: 'regularFont';
}
.imgAboutUs img  {
    /* background-image: url(../../../../../public/img/aboutUsImg.png); */
    background-size: cover;
     background-repeat: no-repeat;
