     width: 100%; */
}
.frameText {
    font-weight: 600;
    color: #000;
    font-family: 'regularFont';
    width: 90%;
    text-align: left;
}

.circul{
    border-radius: 50%;
    background-color: #000;
    width: 100px;
    height: 100px;
    position: relative;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
}


.circul span {
    position: absolute;
    font-size: 30px;
}
@media (max-width: 885px) {
    .frameText {
        width: 100%;
    }
}